
.c-donations-featured {

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 3.4375rem $s-gutter--edge-mobile 3.125rem;
  }

  &__image-wrapper {
    width: 85%;
  }

  &__image-container {
    padding-bottom: 67.25%;
  }

  &__image-svg-wrapper {
    position: relative;
    margin-bottom: 5rem;

    svg {
      position: absolute;
      bottom: -13%;
      z-index: -1;
      right: 0;
      width: 53.8%;
    }
  }

  &__content {
    padding: 0 1.5rem 0 1.25rem;
  }

  &__link {
    margin-top: 0.625rem;
    display: inline-block;
  }

  @include t-media-query($s-small-up) {
    &__inner {
      padding: 3.4375rem 6.5% 3.125rem;
    } 
  }

  @include t-media-query($s-large-up) {
    &__inner {
      padding: 8.375rem 6.5%;
      display: grid;
      grid-template-columns: 58% 29%;
      grid-gap: 7%;
      align-items: start;
    }

    &__content{
      margin-top: 2rem;
      max-width: 29.375rem;
    }

    &__title{
      margin-bottom: 1.5rem;
    }

    &__image-svg-wrapper {
  
      svg {
        bottom: -14%;
      }
    }

    &__link{
      margin-top: 2.5rem;
    }
  }
}